import Toast from '@/wxcomponents/vant-weapp/dist/toast/toast';
export default {
	data() {
		return {
			isLogin: true,
			username: '',
			password: '',
			againPwd: ''
		}
	},
	methods: {
		// 切换登录注册状态
		loginOrregister() {
			this.isLogin = !this.isLogin
			this.password=''
			this.username=''
			this.againPwd=''
		},
		// 登录
		loginByAccount() {
			if (this.username == '' || this.password == '') {
				Toast('请输入用户名或者密码');
				return
			}
			this.$minApi.loginByAccount({
				password: this.password,
				username: this.username,
				userSchoolId: '1',
				userIdenty: '0'
			}).then(data => {
				if (data.code == 200) {
					uni.setStorageSync('token', data.data.token),
						uni.switchTab({
							url: '../../my/index'
						})
					Toast(data.msg);
				} else {
					Toast(data.msg);
				}

			})
		},
		// 注册
		registerUserInfo() {
			if (this.username.trim() == '' || this.password.trim() == '' || this.againPwd.trim() == '') {
				uni.showToast({
					title: '用户名或密码不能为空！',
					icon: 'none'
				})
			} else {
				if (this.password == this.againPwd) {
					this.$minApi.registerUserInfo({
						username: this.username,
						password: this.againPwd,
						userIdenty: 0,
					}).then(data => {
						if (data.code == 200) {
							uni.navigateTo({
								url: '../jionClass/index?studentId=' + data.data
							})
						} else {
							uni.showToast({
								title: data.msg,
								icon: 'none'
							})
						}

					})
				} else {
					uni.showToast({
						title: '两次密码不一致！',
						icon: 'none'
					})
				}

			}

		}
	},
	created() {

	}
}
